@use 'base/variables' as v;
@use 'base/mixins' as m;

.barra {

    background-color: v.$azul;

    &__contenido {

        @include m.contenedor-sm;
        display: flex;
        flex-direction: column;
        @include m.desktop {

            flex-direction: row;
            justify-content: space-between;
            align-items: center;
            
        }

    }

    &__logo {

        @include m.logo;

    }

}